<template>
    <div v-if="createdUser" class="alert alert-success" role="alert">
        Аккаунт успешно создан. <router-link to="signin" class="alert-link">Авторизуйтесь.</router-link>
    </div>
    <CreateProfile v-on:profile-has-been-created="profileHasBeenCreated($event)"/>
</template>

<script>
import CreateProfile from '@/components/CreateProfile.vue'

export default {
    components: { CreateProfile },
    data(){
        return{
            createdUser: undefined
        }
    },
    methods:{
        profileHasBeenCreated(createdUser){
            this.createdUser = createdUser
        }
    }
}
</script>