<template>
    
    <div class="mb-3">
        <label for="login" class="form-label">Логин</label>
        <input type="text" class="form-control" id="login" v-model="login">
    </div>
    <div class="mb-3">
        <label for="password" class="form-label">Пароль</label>
        <input type="password" class="form-control" id="password" v-model="password">
    </div>
    <div class="mb-3">
        <label for="password-check" class="form-label">Повторите пароль</label>
        <input type="password" class="form-control" id="password-check" v-model="passwordCheck">
    </div>
    <template v-if="authUser">
        <div class="mb-3">
            <label for="tariff" class="form-label">Тарифный план</label><br>
            <select class="form-select" id="tariff" v-model="tariff">
                <option value="1">1</option>
                <option value="2">2</option>
            </select>
        </div>
        <div class="mb-3">
            <label for="role" class="form-label">Роль</label><br>
            <select class="form-select" v-model="role">
                <option value="user">пользователь</option>
                <option value="admin">администратор</option>
            </select>
        </div>
    </template>
    <button class="btn btn-primary" @click="createProfile()" :class="(passwordCheck!==password || password==='') ? 'disabled' : ''">Создать аккаунт</button>
    
</template>

<script>
import Axios from '@/axios'
import { mapGetters } from 'vuex'

export default {
    data(){
        return{
            login: "",
            tariff: "1",
            role: "user",
            password: "",
            passwordCheck: ""
        }
    },
    computed:{
        ...mapGetters(['authUser']),
    },
    methods: {
        async createProfile(){

            const { login, password, role, tariff } = this

            const res = (await Axios.post("users", {
                login,
                password,
                role,
                tariff
            })).data

            if(res.error){
                alert(res.error)
            }else{
                this.$emit('profile-has-been-created', res)
            }
        }
    }
}
</script>